import { useState } from "react";
import About from "../components/about";
import Dialog from "../components/dialog/dialog";

const Header = () => {
  const [showInfo, setShowInfo] = useState(false);

  const PUB_URL = process.env.PUBLIC_URL;

  return <div className='headerPan'>
    <div className='flexCenter fullWidth paddingHoriz'>
      <div className='headerName' title=' 1.4.1'>
        <img className='headerIcon' src={PUB_URL + '/logo512.png'} alt='point' />
        <div className='paddingLeft05'>   </div>
      </div>
      <div className='flexGrow'></div> {/* spacer */}
      <div className='headerBlock' title='' onClick={() => setShowInfo(true)}>
        <img className='headerIcon' src={'PUB_URL + /images/information.svg'} alt='point' />
      </div>
      {showInfo && <Dialog title='   ' onClose={() => setShowInfo(false)}>
        <About/>
      </Dialog>}
    </div>
  </div>
};

export default Header;