import { showOkDialog } from '../../storages/appStorage';
import { setEditingEl, setAssemblingEl, changeAssemblingEl, deleteElement, 
  setSelectedName } from '../../storages/elementsStorage';
import { useMapStore, getDrawing, deleteDrawing, expandAllClusters } from '../../storages/mapStorage';
import { LocateIcon, EditIcon, DeleteIcon  } from '../common';
import { elType } from '../../utis';

const ExpElement = ({element}) => {
  const map = useMapStore(state => state.map);

  const onLocate = () => {
    const drawing = getDrawing(element.name);
    if (map && drawing) drawing.locate(map);
  };

  const onEdit = () => {
    setEditingEl(element);
    setAssemblingEl(element);

    const drawing = getDrawing(element.name);
    if (map && drawing) {
      drawing.locate(map);
      map.setZoom(10);
      expandAllClusters();
      drawing.startEdit((event) => {
        if (element.type == elType.POINT) {
          if (event.latLng) {
            const lat = event.latLng.lat();
            const lng = event.latLng.lng();
            changeAssemblingEl({coordinates: {lat, lng}});
          }
        } else {
            const path = drawing.getPath();
            changeAssemblingEl({coordinates: path});
          }
      });
    }
  };

  const doDelete = () => {
    setSelectedName(null);
    deleteDrawing(element.name);
    deleteElement(element.name);
  };

  const onDelete = () => showOkDialog({
    title: ' ', 
    message: `  "${element.name}"?`, 
    onOK: doDelete});

  return <div className='flexElem'>
    <div className='flexGrow'>
      <div className='expText boldText'>
        {element.name}
      </div>
      <div className='expText leftAlign paddingHoriz'>
        {element.descr}
      </div>
    </div>
    <div className='flexColumn'>
      <LocateIcon title='  ' onClick={onLocate}/>
      <EditIcon title=' ' onClick={onEdit}/>
      <DeleteIcon title=' ' onClick={onDelete}/>
    </div>
  </div>;
};

export default ExpElement;