import create from 'zustand';
import { showOkDialog } from './appStorage';
import { deleteDrawing, clearClustererMarkers } from './mapStorage';
import { elType, mapReplacer, mapReviver, getRandomAmounts, addRandomElements } from '../utis';

const initElements = () => elementsStoreApi.getState().initElements();
const setEditingEl =    (element) => elementsStoreApi.getState().setEditingEl(element);
const setAssemblingEl = (element) => elementsStoreApi.getState().setAssemblingEl(element);
const getAssemblingEl = () => elementsStoreApi.getState().assemblingEl;
const changeAssemblingEl = (fields) => elementsStoreApi.getState().changeAssemblingEl(fields);
const setElements = (elements) => elementsStoreApi.getState().setElements(elements);
const getElements = () => elementsStoreApi.getState().elements;
const saveElement = (name2del) => elementsStoreApi.getState().saveElement(name2del);
const deleteElement = (name) => elementsStoreApi.getState().deleteElement(name);
const deleteAllShown = () => elementsStoreApi.getState().deleteAllShown();
const setTypeFilter = (type) => elementsStoreApi.getState().setTypeFilter(type);
const getTypeFilter = () => elementsStoreApi.getState().typeFilter;
const setSelectedName = (name) => elementsStoreApi.getState().setSelectedName(name);
const setRandomAmount = (n) => elementsStoreApi.getState().setRandomAmount(n);
const getRandomAmount = () => elementsStoreApi.getState().randomAmount;
const generateRandom = () => elementsStoreApi.getState().generateRandom();
const setOrder = (orderBy) => elementsStoreApi.getState().setOrder(orderBy);

const [useElementsStore, elementsStoreApi] = create((set, get) => ({
  initialized: false,
  elements: new Map(),
  editingEl: null,
  assemblingEl: null,
  typeFilter: new Set([elType.POINT, elType.LINE, elType.POLYGON]),
  selectedName: null,
  randomAmount: 5,
  orderBy: 0,
  orderAsc: true,

  initElements: () => {
    const initialized = get().initialized;
    if (!initialized) {
      const jsonElements = localStorage.getItem('elements');
      const jsonFilter = localStorage.getItem('typeFilter');
      const orderByStr = localStorage.getItem('orderBy');
      const orderAscStr = localStorage.getItem('orderAsc');

      try {    
        const restoredElements = JSON.parse(jsonElements, mapReviver);
        if (restoredElements) set({ elements: restoredElements });
      } catch (err) {
        console.error('Fail to restore elements: ' + err);
      }

      if (jsonFilter) {
        try {    
          const restoredFilter = JSON.parse(jsonFilter);
          if (restoredFilter.typeFilter) (set({ typeFilter: new Set(restoredFilter.typeFilter) }));

        } catch (err) {
          console.error('Fail to restore typeFilter: ' + err);
        }
      }

      const orderBy = orderByStr ? Number(orderByStr) : 0;
      const orderAsc = orderAscStr ? orderAscStr === 'true' : true;
      set({ orderBy: orderBy, orderAsc: orderAsc});

      set({ initialized: true });
    }
  },

  setEditingEl:   (element) => set({editingEl: element}),
  setAssemblingEl: (element) => set({assemblingEl: element}),

  changeAssemblingEl: (fields) => {
    const changedAE = Object.assign({}, get().assemblingEl, fields);
    set({ assemblingEl: changedAE });
  },

  setElements: (elements) => {
    set({elements: elements});

    const jsonElements = JSON.stringify(elements, mapReplacer);
      localStorage.setItem('elements', jsonElements);
  },

  saveElement: (name2del) => {
    const {elements, assemblingEl} = get();

    elements.delete(name2del); // delete old element when name was changed

    const nameTr = assemblingEl.name?.trim();
    if (nameTr) {
      const descrTr = assemblingEl.descr?.trim();
      assemblingEl.name = nameTr;
      assemblingEl.descr = descrTr;
      assemblingEl.type = assemblingEl.type ? assemblingEl.type : elType.POINT;
      elements.set(nameTr, assemblingEl);

      const jsonElements = JSON.stringify(elements, mapReplacer);
      localStorage.setItem('elements', jsonElements);

      set({ elements: new Map(elements) });
    }
  },

  deleteElement: (name) => {
    const elements = get().elements;
    if (elements.delete(name)) {
      const jsonElements = JSON.stringify(elements, mapReplacer);
      localStorage.setItem('elements', jsonElements);

      set({ elements: new Map(elements) });
    }
  },

  deleteAllShown: () => {
    const {elements, typeFilter} = get();

    const newElements = new Map();

    elements.forEach((el) => {
      if (!typeFilter.has(el.type)) {
        newElements.set(el.name, el);
        elements.delete(el.name);
      }
    });

    for (let name of elements.keys()) { // hide the rest
      deleteDrawing(name);
    }

    const jsonElements = JSON.stringify(newElements, mapReplacer);
    localStorage.setItem('elements', jsonElements);

    set({ elements: newElements });
  },

  setTypeFilter: (type) => {
    const filter = get().typeFilter;
    let newFilter = new Set(filter);
    if (newFilter.has(type)) {
      if (type == elType.POINT) clearClustererMarkers();
      newFilter.delete (type);
    } else {
      newFilter.add(type);
    }

    const jsonFilter = JSON.stringify({typeFilter: Array.from(newFilter)});
    localStorage.setItem('typeFilter', jsonFilter);

    set({ typeFilter: newFilter })
  },

  setSelectedName: (name) => set({selectedName: name}),
  setRandomAmount: (n) => set({randomAmount: n}),

  generateRandom: () => {
    const {elements, randomAmount} = get();
    const amounts = getRandomAmounts(randomAmount);
    const newElements = addRandomElements(elements, amounts);

    showOkDialog({
      title: ' ', 
      message: ` : ${amounts.points}, : ${amounts.lines}, : ${amounts.polygons}.   : ${randomAmount}.`});

    const jsonElements = JSON.stringify(newElements, mapReplacer);
    localStorage.setItem('elements', jsonElements);

    set({ elements: newElements });
  },

  setOrder: (orderBy) => {
    const currentOB = get().orderBy;
    if (currentOB === orderBy) {
      const orderAsc = get().orderAsc;
      set({ orderAsc: !orderAsc });
      localStorage.setItem('orderAsc', !orderAsc);
    } else {
      set({ orderBy: orderBy, orderAsc: true });
      localStorage.setItem('orderBy', orderBy);
      localStorage.setItem('orderAsc', true);
    }
  }
}));

export {
  useElementsStore,
  initElements,
  setEditingEl,
  setAssemblingEl,
  getAssemblingEl,
  changeAssemblingEl,
  setElements,
  getElements,
  saveElement,
  deleteElement,
  deleteAllShown,
  setTypeFilter,
  getTypeFilter,
  setSelectedName,
  setRandomAmount,
  getRandomAmount,
  generateRandom,
  setOrder
};