import create from 'zustand';
import { Loader } from 'google-maps';
import MarkerClustererWrap from '../components/map/markerClustererWrap';

const initGoogleMap = (params) => mapStoreApi.getState().initGoogleMap(params);
const cleanUpMapState = () => mapStoreApi.getState().cleanUpMapState();
const saveDrawing = (name, drawing) => mapStoreApi.getState().saveDrawing(name, drawing);
const getDrawing = (name) => mapStoreApi.getState().drawings.get(name)
const deleteDrawing = (name) => mapStoreApi.getState().deleteDrawing(name);
const setTempDrawing = (drawing) => mapStoreApi.getState().setTempDrawing(drawing);
const getTempDrawing = () =>     mapStoreApi.getState().tempDrawing;
const clusterMarkers = (markers) => mapStoreApi.getState().clusterMarkers(markers);
const clearClustererMarkers = () => mapStoreApi.getState().clearClustererMarkers();
const expandAllClusters = () => mapStoreApi.getState().expandAllClusters();

const [useMapStore, mapStoreApi] = create((set, get) => ({
  map: null,
  markerClusterer: null,
  mapLoading: 'not loaded',
  drawings: new Map(),
  tempDrawing: null, // temp object to be cleaned

  initGoogleMap: ({center, zoom}) => {

    const mapLoading = get().mapLoading;

    if (mapLoading != 'loading' && mapLoading != 'done') {
      set({ mapLoading: 'loading' });
      const loader = new Loader('AIzaSyAeM9hAb_UitOiVfNKxxR3wIWwnvU7m-LY');
      loader.load().then(() => {
        const googleMap = new window.google.maps.Map(
          document.getElementById('map'), 
          {
            center: center,
            zoom: zoom,
          }
        );
        set({ 
          map: googleMap, 
          markerClusterer: new MarkerClustererWrap(googleMap), 
          mapLoading: 'done' });
      });
    }
  },

  cleanUpMapState: () => {
    const {map, markerClusterer} = get();
    if (map) {
      map.setOptions({draggableCursor: ''});  // set default cursor
      if (markerClusterer) markerClusterer.expandAllClusters(false);
  
      // drop listeners
      window.google.maps.event.clearListeners(map, 'click'); 
      window.google.maps.event.clearListeners(map, 'mousemove');
    
      // remove temp markers
      const tempDrawing = get().tempDrawing;
      if (tempDrawing) {
        tempDrawing.remove();
        set({ tempDrawing: null });
      }  
    }
  },

  saveDrawing: (name, drawing) => {
    const drawings = get().drawings;
    drawings.set(name, drawing);
  },

  deleteDrawing: (name) => {
    const {drawings, markerClusterer} = get();
    const drawing = drawings.get(name);
    if (drawing) {
      if (markerClusterer) markerClusterer.unClusterMarker(drawing.mapobj);
      drawing.remove();
      drawings.delete(name);
    }
  },

  setTempDrawing: (drawing) => set({ tempDrawing: drawing }),

  clusterMarkers: (markers) => {
    const markerClusterer = get().markerClusterer;
    if (markerClusterer) markerClusterer.clusterMarkers(markers);
  },

  clearClustererMarkers: () => {
    const markerClusterer = get().markerClusterer;
    if (markerClusterer) markerClusterer.clearMarkers();
  },

  expandAllClusters: () => {
    const markerClusterer = get().markerClusterer;
    if (markerClusterer) markerClusterer.expandAllClusters(true);
  },
}));

export {
  useMapStore,
  initGoogleMap,
  cleanUpMapState,
  saveDrawing,
  getDrawing,
  deleteDrawing,
  setTempDrawing,
  getTempDrawing,
  clusterMarkers,
  clearClustererMarkers,
  expandAllClusters
};
