import { useMemo } from 'react';
import RvTable from '../rvTable/rvTable';
import { useElementsStore, setSelectedName, setOrder } from '../../storages/elementsStorage';
import ExpElement from './expElement';
import { SortIcon } from '../common';
import { elType, ascComparator } from '../../utis';


const ElementsList = () => {
  const elements = useElementsStore(state => state.elements);
  const typeFilter = useElementsStore(state => state.typeFilter);
  const selectedName = useElementsStore(state => state.selectedName);
  const orderBy = useElementsStore(state => state.orderBy);
  const orderAsc = useElementsStore(state => state.orderAsc);

  const type2sortAlias = (type) => {  // a_, b_, c_ for sorting
    switch (type) {
      case elType.POINT: return 'a_point';
      case elType.LINE: return 'b_line';
      default: return 'c_polygon';
    }
  };

  const sortAlias2filePath = (typeAlias) => {
    const path = `${process.env.PUBLIC_URL}/images/`
    switch (typeAlias) {
      case 'a_point': return path + 'pointSelected.svg';
      case 'b_line': return path + 'lineSelected.svg';
      default: return path + 'polygonSelected.svg';
    }
  };

  const getRows = () => {
    const forSort = [];
    elements.forEach((el) => {
      if (typeFilter.has(el.type)) {
        forSort.push([el.name, type2sortAlias(el.type), el.descr ? el.descr : '']);
      }
    });

    forSort.sort( (a, b) => orderAsc 
        ? ascComparator(a[orderBy], b[orderBy]) 
        : -ascComparator(a[orderBy], b[orderBy]) );

    const rows = forSort.flatMap((row) => [
      [
        <div className='textCell'>{row[0]}</div>, // el.name
        <img className='buttonIcon' src={sortAlias2filePath(row[1])} alt={row[1]} />, // typeAlias
        <div className='textCell'>{row[2]}</div>  // el.descr
      ],
      [row[0], '', ''] // exp row
    ]);

    return rows;
  };
  const rows = useMemo(getRows, [elements, typeFilter, orderBy, orderAsc]);

  const getSortIcon = (ind) => {
    const active = orderBy == ind;
    const asc = !active || orderAsc;
    return <SortIcon active={active} asc={asc} onClick={()=>setOrder(ind)} />;
  };

  const getHeader = () => [
    <div className='sortHeader'>{getSortIcon(0)}</div>,
    <div className='sortHeader'>{getSortIcon(1)}</div>,
    <div className='sortHeader'>{getSortIcon(2)}</div>
  ];
  const header = useMemo(getHeader, 
    [orderBy, orderAsc]);

  const getExpEl = () => {
    const element = elements.get(selectedName);
    if (element) {
      return <ExpElement element={element}/>;
    }
    return <div>{`   ${selectedName} `}</div>;
  };
  
  const getColumnWidth = ({index, listWidth}) => {
    const fixedColumns = 150; // Name + Type
    const freeSpace = listWidth - fixedColumns;
    const name = freeSpace > 10 ? freeSpace : 10;
    switch (index) {
      case 0: return 100;  // Name
      case 1: return 50;  // Type
      case 2: return name;  // Desc
    }
    return 0;
  };

  const toggleRowOpen = (name) => {
    if (name == selectedName) {
      setSelectedName(null);
    } else {
      setSelectedName(name);
    }
  };

  return <RvTable rows={rows} header={header} columnCount={3} clickColmns={[0,1,2]} selectedName={selectedName} 
    orderBy={orderBy} orderAsc={orderAsc} onRowClick={toggleRowOpen} rowHeight={32} 
    expElHeight={80} getExpEl={getExpEl} getColumnWidth={getColumnWidth}/>;
};

export default ElementsList;