import { useEffect, useMemo, useState } from 'react';
import { TextField, FormControl, InputLabel, MenuItem, Select, List, ListItem } from '@mui/material';
import { getDrawing, deleteDrawing, useMapStore } from '../../storages/mapStorage';
import { useElementsStore, changeAssemblingEl, saveElement, setSelectedName } from '../../storages/elementsStorage';
import TitleWithClose from "../dialog/titleWithClose";
import { ButtonColored } from '../common';
import { createTempDrawing } from '../map/mapUtils';
import { elType, coordsDiffer } from '../../utis';

const EditElement = ({onClose}) => {
  const map = useMapStore(state => state.map);
  const editingEl = useElementsStore(state => state.editingEl);
  const assemblingEl = useElementsStore(state => state.assemblingEl);
  const elements = useElementsStore(state => state.elements);

  const nameVal = assemblingEl.name ? assemblingEl.name : '';
  const descrVal = assemblingEl.descr ? assemblingEl.descr : '';
  const typeVal = assemblingEl.type ? assemblingEl.type : elType.POINT;


  const [nameErr, setNameErr] = useState(null);

  const editMode = Boolean(editingEl);
  const title = editMode ? ` "${editingEl.name}"` : ' ';

  const onInputName = (e) => {
    
    changeAssemblingEl({name: e.target.value});

    const name = e.target.value.trim();
    if (!name) {
      setNameErr('    ');
      return;
    }
    const nameChanged = name != editingEl?.name;
    if (nameChanged && elements.has(name)) {
      setNameErr('   ');
      return;
    }
    setNameErr(null);
  }

  const onInputDescr = (e) => {
    changeAssemblingEl({descr: e.target.value});
  }

  const onInputType = (e) => {
    const newType = e.target.value;
    if (newType != assemblingEl?.type) {
      createTempDrawing(map, newType);
    }
  };

  const closeEdit = (restorePos) => {
    const drawing = getDrawing(editingEl?.name);
    if (drawing) {
      drawing.stopEdit(restorePos);
    }
    onClose();
  };

  const onSave = () => {
    if (editMode && assemblingEl.name != editingEl.name) {
      saveElement(editingEl.name);
      deleteDrawing(editingEl.name);
    } else {
      saveElement();
    }
    setSelectedName(assemblingEl.name);
    closeEdit(false);
  };

  const isPoint = assemblingEl?.type == elType.POINT;

  const getCoordsHint = () => {
    if (editMode) return '( ' + (isPoint ? ')' : ',   - )');
    return '(  ' + (isPoint ? ')' : ',   - )');
  };

  const coordsHint = useMemo(getCoordsHint, [isPoint]);

  const getCoordsText = (assemblingEl) => {
    if (assemblingEl?.coordinates?.lat && assemblingEl?.coordinates?.lng)
      return `${assemblingEl.coordinates.lat},   ${assemblingEl.coordinates.lng}`
    return ''};
    
  const coordsText = useMemo(() => getCoordsText(assemblingEl),
    [assemblingEl]);

  const scrollToLastCoordItem = () => {
    if (!isPoint && assemblingEl?.coordinates) {
      const lastItemID = `coordId${assemblingEl.coordinates.length - 1}`;
      const lastItem = document.getElementById(lastItemID);
        if (lastItem) lastItem.scrollIntoView({ block: 'center', behavior: 'smooth' });
    }
  }
  useEffect(() => {
    scrollToLastCoordItem();
  }, [assemblingEl]);

  const getCoordList = (assemblingEl) => {
    if (!isPoint && assemblingEl?.coordinates) {
      return assemblingEl.coordinates.map(
        (point, index) => <ListItem key={index} id={`coordId${index}`}>
          {`${point.lat}   ${point.lng}`}</ListItem>);
    }
    return [];
  }
  const coordListItems = useMemo(() => getCoordList(assemblingEl),
    [assemblingEl]);

  const isCoordsCorrect = (type) => {
    switch (type) {
      case elType.POINT:
        return assemblingEl.coordinates;
      case elType.LINE:
        return assemblingEl.coordinates?.length > 1;
      default:
        return assemblingEl.coordinates?.length > 2;
    }
  };
  const coordsCorrect = useMemo(() => isCoordsCorrect(assemblingEl.type), [assemblingEl]);

  const isChanged = () => {
    if (editingEl) {
      return assemblingEl.name != editingEl.name ||
        assemblingEl.descr != editingEl.descr ||
        coordsDiffer(assemblingEl.coordinates, editingEl.coordinates);
    }
    return assemblingEl.name && assemblingEl.coordinates;
  };
  const changed = useMemo(isChanged, [assemblingEl]);

  const canSave = !nameErr && changed && coordsCorrect;

  const PUB_URL = process.env.PUBLIC_URL;

  return <div className='flexColumn paddingHoriz'>
    <TitleWithClose onClose={ ()=>closeEdit(true) }>
      {title}
    </TitleWithClose>
    <TextField
      variant='outlined'
      margin='normal'
      label=''
      value={nameVal}
      error={Boolean(nameErr)}
      helperText={nameErr}
      onChange={onInputName}
    />
    <TextField
      variant='outlined'
      multiline rows={3}
      margin='normal'
      label=''
      value={descrVal}
      onChange={onInputDescr}
    />
    <FormControl fullWidth>
      <InputLabel id="type-select-label"></InputLabel>
      <Select
        label=""
        labelId="type-select-label"
        value={typeVal}
        disabled={editMode}
        onChange={onInputType}
      >
        <MenuItem value={elType.POINT}>
          <div className='flexCenter'>
            <img className='buttonIcon' src={`${PUB_URL}/images/${editMode?'pointUnselected':'pointSelected'}.svg`} alt={'point'} />
            <div className='paddingLeft05'></div>
          </div>
        </MenuItem>
        <MenuItem value={elType.LINE}>
          <div className='flexCenter'>
            <img className='buttonIcon' src={`${PUB_URL}/images/${editMode?'lineUnselected':'lineSelected'}.svg`} alt={'line'} />
            <div className='paddingLeft05'></div>
          </div>
        </MenuItem>
        <MenuItem value={elType.POLYGON}>
          <div className='flexCenter'>
            <img className='buttonIcon' src={`${PUB_URL}/images/${editMode?'polygonUnselected':'polygonSelected'}.svg`} alt={'polygon'} />
            <div className='paddingLeft05'></div>
          </div>
        </MenuItem>
      </Select>
    </FormControl>
    <div className='paddingTop leftAlign'>
      <div className='boldText'>:</div>
      <div className='smallText'>{coordsHint}</div>
    </div>
    {isPoint 
      ? <div>{coordsText}</div>
      : <div className='paddingElem'>
          <List className='scrollList'>
            {coordListItems}
          </List>
        </div>
    }
    <div className='paddingTop'>
      <ButtonColored disabled={!canSave} onClick={onSave}></ButtonColored>
    </div>
  </div>
};

export default EditElement;