import { useEffect } from 'react';
import './App.css';
import Header from './layout/header';
import Map from './layout/map';
import Elements from './layout/elements';
import Footer from './layout/footer';
import { initElements } from './storages/elementsStorage';
import { useAppStore, initAppData, showOkDialog } from './storages/appStorage';
import OkDialog from './components/dialog/okDialog';

function App() {
  const okMessage = useAppStore(state => state.okMessage);


  useEffect(() => {
    // init App
    initAppData();
    initElements();
  }, []);

  const onOkDialog = () => {
    const onOK = okMessage.onOK;
    showOkDialog(null);
    if (onOK) onOK();
  };

  return (
    <div className="App">
      <Header/>
      <Map/>
      <Elements/>
      <Footer/>
      {okMessage && 
      <OkDialog 
        title={okMessage.title} 
        message={okMessage.message} 
        onOK={onOkDialog} onClose={()=>showOkDialog(null)}/>}
    </div>
  );
}

export default App;
