import React, { useEffect } from 'react';
import CircularProgress from '@mui/material/CircularProgress';
import { useMapStore, initGoogleMap } from '../storages/mapStorage';
import Drawings from '../components/map/drawings';

const Map = () => {

  const mapLoaded = useMapStore(state => state.mapLoaded);

  const init = () => {
    initGoogleMap({
      center: {lat: 55.74, lng: 37.61},
      zoom:   10,
    });
  };

  useEffect(() => {
    init();
  }, []);

  return <div className='mapPan'>
    {!mapLoaded && <div className="circularProgress"><CircularProgress/></div>}
    <div id='map'></div>
    <Drawings/>
  </div>;
};
export default Map;