import { useState } from 'react';
import DialogButton from './dialog/dialogButton';
import { showOkDialog } from '../storages/appStorage';
import { getElements, setElements } from '../storages/elementsStorage';
import { geojson2Elements } from '../utis';

const ImportFromFileDlg = ({onClose}) => {
  const [file, setFile]   = useState(null);

  const onInputFile = (event) => {
    setFile(event.target.files[0])
  };

  const onOk = () => {
    const elements = getElements();
    const reader = new FileReader();
    reader.onload = function(e) {
      const geojson = e.target.result;

      try {    
        const impElements = geojson2Elements(geojson);
        setElements(new Map([...elements, ...impElements]));
      } catch (err) {
        console.error('Fail to handle geojson: ' + err);
        showOkDialog({
          title: '', 
          message: `   `});
      }

    };
    reader.readAsText(file);
    onClose();
  };
  
  return <DialogButton title={'  '} okDisabled={!file}
    onOK={onOk} onClose={onClose}>
    <div className='flexColumn'>
      <div>   ?</div>
      <div className='smallText'>(        )</div>
      <input className='paddingTop' accept={'.geojson'} onChange={onInputFile} type='file'/>
    </div>
  </DialogButton>;
};
export default ImportFromFileDlg;