
const elType = {
  POINT: 'point',
  LINE: 'line',
  POLYGON: 'polygon'
};

// https://stackoverflow.com/questions/29085197/how-do-you-json-stringify-an-es6-map
const mapReplacer = (key, value) => {
  if(value instanceof Map) {
    return {
      dataType: 'Map',
      value: Array.from(value.entries()), // or with spread: value: [...value]
    };
  } else {
    return value;
  }
};

const mapReviver = (key, value) => {
  if(typeof value === 'object' && value !== null) {
    if (value.dataType === 'Map') {
      return new Map(value.value);
    }
  }
  return value;
};

const ascComparator = (a, b) => {
  if (a > b) return 1;
  if (a < b) return -1;
  return 0;
};

const pointsDiffer = (coord1, coord2) => {
  return coord1.lat !== coord2.lat || coord1.lng !== coord2.lng;
};

const coordsDiffer = (coord1, coord2) => {
  if (Array.isArray(coord1) && Array.isArray(coord2)) {
    if (coord1.length != coord2.length) return true;

    return coord1.some((point, index) => pointsDiffer(point, coord2[index]));
  }
  return pointsDiffer(coord1, coord2);
};

const isNumber = (val) => {
  if (val === 0) return true;
  if (val === true) return false;
  return val && isFinite(val);
}


// ------------------------------ import/export ------------------------------

const elCoords2geo = (coords, polygon) => {
  if (!coords?.length) return [];
  const geoCoords = coords.map((coord) => [coord.lng, coord.lat]);
  if (polygon) {
    geoCoords.push(geoCoords[0]);
    return [geoCoords];
  }
  return geoCoords;
};

const geoCoords2PointCoords = (geoCoords) => {
  if (geoCoords.length != 2) return null;

  const lng = geoCoords[0];
  const lat = geoCoords[1];
  if (!isNumber(lat) || !isNumber(lng)) return null;

  return {lat, lng};
};

const geoCoords2LineCoords = (geoCoords) => {
  if (geoCoords.length < 2) return null;

  const coords = geoCoords.map((coord) => geoCoords2PointCoords(coord));
  if (coords.some((coord => !coord))) return null;

  return coords;
};

const geoCoords2PolyCoords = (geoCoords) => {
  if (!geoCoords.length) return null;

  const coords = geoCoords2LineCoords(geoCoords[0]);
  coords.pop();
  
  if (coords.length < 3) return null;
  return coords;
};

const el2geometry = (el) => {
  if (el.type == elType.POINT) {
    return {
      type: 'Point',
      coordinates: [el.coordinates.lng, el.coordinates.lat]
    };
  }

  if (el.type == elType.LINE) {
    return {
      type: 'LineString',
      coordinates: elCoords2geo(el.coordinates)
    };
  }

  return {
    type: 'Polygon',
    coordinates: elCoords2geo(el.coordinates, true)
  };
};

const el2feature = (el) => ({
  type: 'Feature',
  geometry: el2geometry(el),
  properties: {
    name: el.name,
    description: el.descr
  }
});

const feature2el = (feature) => {
  const coordinates = feature.geometry?.coordinates;
  if (!Array.isArray(coordinates)) return null;

  const descr = feature.properties.description ?? '';

  switch (feature.geometry?.type) {
    case 'Point':
      const pointCoords = geoCoords2PointCoords(coordinates);
      if (!pointCoords) return null;
      return {
        name: feature.properties.name,
        descr: descr,
        type: elType.POINT,
        coordinates: pointCoords
      };

    case 'LineString':
      const lineCoords = geoCoords2LineCoords(coordinates);
      if (!lineCoords) return null;
      return {
        name: feature.properties.name,
        descr: descr,
        type: elType.LINE,
        coordinates: lineCoords
      };

    case 'Polygon':
      const polyCoords = geoCoords2PolyCoords(coordinates);
      if (!polyCoords) return null;
      return {
        name: feature.properties.name,
        descr: descr,
        type: elType.POLYGON,
        coordinates: polyCoords
      };
      
    default:
      return null;
  }
};

const elements2Geojson = (elements, typeFilter) => {
  const geo = {type: 'FeatureCollection', features: []}
  elements.forEach((el) => {
    if (typeFilter.has(el.type)) {
      geo.features.push(el2feature(el));
    }
  });
  return JSON.stringify(geo);
};

const geojson2Elements = (geojson) => {
    const obj = JSON.parse(geojson);
    const features = obj?.features;
    if (!Array.isArray(features)) throw 'No features array';

    const elements = new Map();

    features.forEach((feature) => {
      const name = feature.properties?.name;
      if (name) {
        const el = feature2el(feature);
        if (el) elements.set(name, el);
      }
    });

    return elements;
};

const downloadFile = (fileName, blob) => {
  const link = document.createElement('a');
  link.download = fileName;
  link.href = URL.createObjectURL(blob);
  link.click();
  URL.revokeObjectURL(link.href);
};


// ------------------------------ random generation ------------------------------
const namSymbs = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz';

const getRandomNumber = (min, max, int) => {
  const nmb = min + Math.random() * (max-min);
  return int ? Math.round(nmb) : nmb;
};

const createRandomName = (length) => {
  let name = '';
  for (let i = 0; i < length; i++) {
    const ind = getRandomNumber(0, namSymbs.length-1, true);
    name = name + namSymbs[ind];
  }
  return name;
};

const getPrefix = (type) => {
  switch (type) {
    case elType.POINT:
      return 'pnt-';
    case elType.LINE:
      return 'ln-';
    default:
      return 'plg-';
  }
};

const createUniqRandomName = (elements, type) => {
  let nameLength = 3;
  while (true) {
    const name = getPrefix(type) + createRandomName(nameLength++);
    if (!elements.has(name)) return name;
  }
};

const createRandomPointCoords = () => {
  const lat = getRandomNumber(-85, 85); // not (-90, 90); google handle polar coords in a strange way
  const lng = getRandomNumber(-180, 180);
  return {lat, lng};
};

const randomAlterCoordVal = (val, scale, maxAbs) => {
  const plus = getRandomNumber(0, 1, true);
  const delta = getRandomNumber(scale, 10 * scale);

  let changedVal = plus ? val + delta : val - delta;
  if (Math.abs(changedVal) > maxAbs) {
    changedVal = plus ? val - delta : val + delta; // invert +/-
  }
  return changedVal; 
};

const createRandomLineCoords = (type) => {
  const vertexNumb = type == elType.LINE ? getRandomNumber(2, 4, true) : getRandomNumber(3, 5, true);
  const scale = getRandomNumber(0.001, 0.1); // house scale - city scale
  const firstPoint = createRandomPointCoords();
  const coords = [firstPoint];

  let nextLat = firstPoint.lat;
  let nextLng = firstPoint.lng;

  for (let i = 1; i < vertexNumb; i++) {
    nextLat = randomAlterCoordVal(nextLat, scale, 85); // not 90; google handle polar coords in a strange way
    nextLng = randomAlterCoordVal(nextLng, scale, 180);

    coords.push({lat: nextLat, lng: nextLng});
  }

  return coords;
};

const createRandomCoords = (type) => {
  if (type == elType.POINT) return createRandomPointCoords();
  return createRandomLineCoords(type);
};

const getDescr = (type) => {
  switch (type) {
    case elType.POINT:
      return '  ';
    case elType.LINE:
      return '  ';
    default:
      return '  ';
  }
};

const createRandomElement = (elements, type) => {
  const name = createUniqRandomName(elements, type);

  return {name: name, type: type, coordinates: createRandomCoords(type), 
    descr: getDescr(type)};
};

const pointsWeight = 0.5;
const linesWeight = 0.3;

const getRandomAmounts = (amount) => {
  const pointDelta = getRandomNumber(-0.2, 0.2);
  const lineDelta =  getRandomNumber(-0.2, 0.2);

  const pointFactor = pointsWeight + pointDelta;
  const lineFactor =  linesWeight + lineDelta;

  const points = Math.round(amount * pointFactor);
  let lines = 0;
  let polygons = 0;

  if ((pointFactor + lineFactor) < 1) {
    lines = Math.round(amount * lineFactor);
    polygons = amount - points - lines;
  } else {
    lines = amount - points;
  }

  return {points, lines, polygons};
};

const addRandomElements = (elements, amounts) => {
  const newElements = new Map(elements);

  console.log(`--- amounts`, amounts);

  for (let i = 0; i < amounts.points; i++) {
    const element = createRandomElement(newElements, elType.POINT);
    newElements.set(element.name, element);
  }

  for (let i = 0; i < amounts.lines; i++) {
    const element = createRandomElement(newElements, elType.LINE);
    newElements.set(element.name, element);
  }

  for (let i = 0; i < amounts.polygons; i++) {
    const element = createRandomElement(newElements, elType.POLYGON);
    newElements.set(element.name, element);
  }

  return newElements;
};

export {
  elType,
  mapReplacer,
  mapReviver,
  ascComparator,
  coordsDiffer,
  elements2Geojson,
  geojson2Elements,
  downloadFile,
  getRandomAmounts,
  addRandomElements
};