import { useState } from 'react';
import { TextField, Checkbox } from '@mui/material';
import { setExpFileName, getExpFileName, setDeleteExport, getDeleteExport } from '../storages/appStorage';
import { getElements, deleteAllShown, getTypeFilter } from '../storages/elementsStorage';
import DialogButton from './dialog/dialogButton';
import { elements2Geojson, downloadFile } from '../utis';

const Export2fileDlg = ({onClose}) => {
  const [fileName, setFileName] = useState(getExpFileName());
  const [delExport, setDdelExport] = useState(getDeleteExport());
  const [nameErr, setNameErr] = useState(null);

  const onInputFName = (e) => {
    setFileName(e.target.value);
    const name = e.target.value.trim();
    if (!name) setNameErr('     ');
    else       setNameErr(null);
  }

  const onOk = () => {
    setExpFileName(fileName);
    setDeleteExport(delExport);
    onClose();

    const elements = getElements();
    const typeFilter = getTypeFilter();

    const geojson = elements2Geojson(elements, typeFilter);
    downloadFile(fileName.trim() + '.geojson', new Blob([geojson], { type: "application/json" }));

    if (delExport) deleteAllShown();
  };

  return <DialogButton title={'  '} okDisabled={!fileName.trim()} 
    onOK={onOk} onClose={onClose}>
    <div className='flexColumn'>
          ?
      <TextField
        variant='outlined'
        margin='normal'
        label=' '
        value={fileName}
        error={Boolean(nameErr)}
        helperText={nameErr}
        onChange={onInputFName}
      />
      <div>
        <Checkbox
          checked={delExport}
          onChange={() => setDdelExport(!delExport)}/>
           
      </div>
    </div>
  </DialogButton>
};
export default Export2fileDlg;