import { useState } from 'react';
import { TextField } from '@mui/material';
import { setRandomAmount, getRandomAmount, generateRandom } from '../storages/elementsStorage';
import DialogButton from './dialog/dialogButton';

const GenerateElementsDlg = ({onClose}) => {
  const [nmbVal, setNmbVal] = useState(getRandomAmount());

  const onInputNumber = (e) => {
    if (e.target.value < 1 || e.target.value > 500) return;
    if (e.target.value.length == 0) {
      setNmbVal(1);
      return;
    }
    setNmbVal(e.target.value);
  };

  const onOk = () => {
    setRandomAmount(nmbVal);
    generateRandom();
    onClose();
  };

  return <DialogButton title={'  '} okName={''} 
    onOK={onOk} onClose={onClose}>
    <TextField 
      label=" (1-500)" 
      variant="outlined" size="small" type="number"
      value={nmbVal} onChange={onInputNumber}/>
  </DialogButton>;
};
export default GenerateElementsDlg;