import { getAssemblingEl, changeAssemblingEl } from '../../storages/elementsStorage';
import { cleanUpMapState, getTempDrawing, setTempDrawing } from '../../storages/mapStorage';
import { PointDrawing, LineDrawing, PolygonDrawing } from './mapObjects';
import { elType } from '../../utis';

// ---------------------- Point ----------------------
const createPointTempDrawing = (map) => {
  const tempPoint = new PointDrawing({icon: {url: process.env.PUBLIC_URL + 'images/map-marker-down.png'}});
  setTempDrawing(tempPoint);
  map.setOptions({draggableCursor: 'crosshair'});

  window.google.maps.event.addListener(map, 'click', function(event) {
    const tempPoint = getTempDrawing();
    if (tempPoint && event.latLng) {
      const lat = event.latLng.lat();
      const lng = event.latLng.lng();

      changeAssemblingEl({coordinates: {lat, lng}});
      tempPoint.update(map, {lat, lng}, false, {title: '   '});
    }
  });
};

// ---------------------- Line, Polygon ----------------------

const getMapTempLine = (type) => {
  const tempDrawing = getTempDrawing();
  if (tempDrawing) return tempDrawing;

  const tempLine = type == elType.POLYGON 
    ? new PolygonDrawing({strokeColor: 'grey'}) 
    : new LineDrawing({strokeColor: 'grey'});
  setTempDrawing(tempLine);
  return tempLine;
};

const clearTempLineListeners = (map) => {
  const tempLine = getMapTempLine();
  window.google.maps.event.clearListeners(tempLine.mapobj, 'click');
  window.google.maps.event.clearListeners(tempLine.mapobj, 'rightclick');
  window.google.maps.event.clearListeners(tempLine.mapobj, 'mousemove');
  window.google.maps.event.clearListeners(map,             'mousemove');
};

const addAssemblingElPoint = (point) => {
  const assemblingEl = getAssemblingEl();
  const coordinates = assemblingEl?.coordinates ? assemblingEl.coordinates : [];
  coordinates.push(point);
  changeAssemblingEl({coordinates: coordinates});
}

const setTempLineListeners = (map, type, line) => {
  if (!line.clickListener) {
    line.clickListener = window.google.maps.event.addListener(line.mapobj, 'click', (e) => {
      if (e.latLng) {
        const lat = e.latLng.lat();
        const lng = e.latLng.lng();

        addAssemblingElPoint({lat, lng});

        const tempLine = getMapTempLine();
        const path = tempLine.getPath();
        path.push({lat, lng});
        tempLine.update(map, path, false);
      }
    });
  }

  if (!line.rgtClkListener) {
    line.rgtClkListener = window.google.maps.event.addListener(line.mapobj, 'rightclick', (e) => {
      if (e.latLng) {
        const lat = e.latLng.lat();
        const lng = e.latLng.lng();

        const tempLine = getMapTempLine();
        const path = tempLine.getPath();
        const prevPoint = path[path.length - 2];
        const lastPoint = path[path.length - 1];

        // if rightclick isn't at click position
        if (prevPoint.lat != lastPoint.lat || prevPoint.lng != lastPoint.lng) {
          addAssemblingElPoint({lat, lng});
          path.push({lat, lng});
          tempLine.update(map, path, false);
        }

        map.setOptions({draggableCursor: ''}); // set default cursor
        tempLine.mapobj.setEditable(false);
        clearTempLineListeners(map);
      }
    });

    // add mousemove listener inside polygon
    if (type == elType.POLYGON && !line.moveListener) {
      line.moveListener = window.google.maps.event.addListener(line.mapobj, 'mousemove', (e) => {
        if (e.latLng) {
          const lat = e.latLng.lat();
          const lng = e.latLng.lng();
  
          const tempLine = getMapTempLine(type);
          const path = tempLine.getPath();

          // moving inside the polygon, so path has more than 2 points
          path[path.length - 1] = {lat, lng};
          tempLine.update(map, path, false);  
        }
      });
    }
  }
};

const initTempLine = (map, type, frstPoint) => {
  window.google.maps.event.clearListeners(map, 'click');

  window.google.maps.event.addListener(map, 'mousemove', (e) => {
    if (e.latLng) {
      const lat = e.latLng.lat();
      const lng = e.latLng.lng();

      const tempLine = getMapTempLine(type);
      const path = tempLine.getPath();

      if (path.length < 2 && (lat != frstPoint.lat || lng != frstPoint.lng) ) {
        tempLine.update(map, [frstPoint, {lat, lng}], false);
        tempLine.mapobj.setEditable(true);
        setTempLineListeners(map, type, tempLine); // A01
      } else {
        path[path.length - 1] = {lat, lng};
        tempLine.update(map, path, false);
      }
    }
  });
};

const createLinePolyTempDrawing = (map, type) => {
  map.setOptions({draggableCursor: 'crosshair'});

  window.google.maps.event.addListener(map, 'click', (e) => {
    if (e.latLng) {
      const lat = e.latLng.lat();
      const lng = e.latLng.lng();

      addAssemblingElPoint({lat, lng});
      initTempLine(map, type, {lat, lng});
    }
  });
};

// ---------------------- any type ----------------------
const createTempDrawing = (map, type) => {
  if (map) {
    cleanUpMapState();
    changeAssemblingEl({type: type, coordinates: null});

    if (type == elType.POINT) {
      createPointTempDrawing(map);
    } else {
      createLinePolyTempDrawing(map, type);
    }
  }
};

export {
  createTempDrawing
};