import React, { useState } from 'react';
import IconButton from '@mui/material/IconButton';
import { useMapStore, cleanUpMapState } from '../storages/mapStorage';
import { useElementsStore, setEditingEl, setAssemblingEl, setTypeFilter } from "../storages/elementsStorage";
import EditElement from '../components/elements/editElement';
import ElementsList from '../components/elements/elementsList';
import Drawer from '../components/drawer';
import { createTempDrawing } from '../components/map/mapUtils';
import Export2fileDlg from '../components/export2fileDlg';
import GenerateElementsDlg from '../components/generateElementsDlg';
import { elType } from '../utis';
import ImportFromFileDlg from '../components/importFromFileDlg';


const Elements = () => {
  const map = useMapStore(state => state.map);
  const assemblingEl = useElementsStore(state => state.assemblingEl);
  const typeFilter = useElementsStore(state => state.typeFilter);
  const showEditPane = Boolean(assemblingEl);
  
  const [showRandom, setShowRandom] = useState(false);
  const [showExport, setShowExport] = useState(false);
  const [showImport, setShowImport] = useState(false);

  const onCloseEdit = () => {
    cleanUpMapState();
    setEditingEl(null);
    setAssemblingEl(null);
  };

  const path = `${process.env.PUBLIC_URL}/images/`;

  const getFilterImgPath = (typeVal) => {
    const selected = typeFilter.has(typeVal);

    switch (typeVal) {
      case elType.POINT: return path + (selected ? 'pointSelected.svg' : 'pointUnselected.svg');
      case elType.LINE: return path + (selected ? 'lineSelected.svg' : 'lineUnselected.svg');
      case elType.POLYGON: return path + (selected ? 'polygonSelected.svg' : 'polygonUnselected.svg');
      default: return '';
    }
  };

  const getFilterImg = (typeVal) => 
    <img className='buttonIcon' src={getFilterImgPath(typeVal)} alt={typeVal} />;

  return <div className='listPan flexColumn'>
    <Drawer open={showEditPane} body={
      (showEditPane) ? <EditElement onClose={onCloseEdit}/> : <></>
    }/>
    {showRandom && <GenerateElementsDlg onClose={() => setShowRandom(false)}/>}
    {showExport && <Export2fileDlg      onClose={() => setShowExport(false)}/>}
    {showImport && <ImportFromFileDlg   onClose={() => setShowImport(false)}/>}
    <div className='elementsHeader'>
      <IconButton className={'iconButton'}
        title='/ '
        onClick={() => setTypeFilter(elType.POINT)}>
        {getFilterImg(elType.POINT)}
      </IconButton>
      <IconButton className={'iconButton'}
        title='/ '
        onClick={() => setTypeFilter(elType.LINE)}
        >
        {getFilterImg(elType.LINE)}
      </IconButton>
      <IconButton className={'iconButton'}
        title='/ '
        onClick={() => setTypeFilter(elType.POLYGON)}
        >
        {getFilterImg(elType.POLYGON)}
      </IconButton>

      <div className='flexGrow'></div> {/* spacer */}
      
      <IconButton className={'iconButton'}
        title='  '
        onClick={() => createTempDrawing(map, elType.POINT)}>
        <img className={'buttonIcon'} src={`${path}mapMarkerPlus.svg`} alt=''/>
      </IconButton>
      <IconButton className={'iconButton'}
        title='   '
        onClick={() => setShowRandom(true)}>
        <img className={'buttonIcon'} src={`${path}mapMarkerMultiple.svg`} alt=''/>
      </IconButton>
      <IconButton className={'iconButton'}
        title='  '
        onClick={() => setShowExport(true)}>
        <img className={'buttonIcon'} src={`${path}export.svg`} alt=''/>
      </IconButton>
      <IconButton className={'iconButton'}
        title='  '
        onClick={() => setShowImport(true)}>
        <img className={'buttonIcon'} src={`${path}import.svg`} alt=''/>
      </IconButton>
    </div>
    <div className='elementsBody'>
      <ElementsList/>
    </div>
  </div>;
}

export default Elements;