import { useMemo } from 'react';
import { useMapStore, getDrawing, saveDrawing, clusterMarkers } from '../../storages/mapStorage';
import { useElementsStore, setSelectedName } from '../../storages/elementsStorage';
import { PointDrawing, LineDrawing, PolygonDrawing } from './mapObjects';
import { elType } from '../../utis';

const Drawings = () => {
  const map = useMapStore(state => state.map);
  const elements = useElementsStore(state => state.elements);
  const typeFilter = useElementsStore(state => state.typeFilter);
  const selectedName = useElementsStore(state => state.selectedName);

  const getElementDrawing = (element) => {
    const oldDrawing = getDrawing(element.name);

    if (typeFilter.has(element.type)) {
      switch(element.type) {
        case elType.POINT:
          if (oldDrawing instanceof PointDrawing) return oldDrawing;
          oldDrawing?.remove();
          return new PointDrawing();
        case elType.LINE:
          if (oldDrawing instanceof LineDrawing) return oldDrawing;
          oldDrawing?.remove();
          return new LineDrawing();
        case elType.POLYGON:
          if (oldDrawing instanceof PolygonDrawing) return oldDrawing;
          oldDrawing?.remove();
          return new PolygonDrawing();
        default:
          return null;
      }
    }
    else {
      oldDrawing?.remove();
      return null;
    }
  };

  const updateElementsOnMap = (map, null2points) => {
    if (map) {

      const markers = [];

      elements.forEach(element => {
        const drawing = getElementDrawing(element);

        if (drawing){
          const options = element.type == elType.POINT ? {title: element.name} : null;
          const mapArg = (null2points && element.type == elType.POINT) ? null : map;
          drawing.update(mapArg, element.coordinates, element.name == selectedName, options);
          drawing.setClickListener(() => setSelectedName(element.name));
          saveDrawing(element.name, drawing);

          if (element.type == elType.POINT) {
            if (!drawing.mapobj) { // show points after init with hidden points; markers wasn't created
              drawing.update(map, element.coordinates, element.name == selectedName, {title: element.name});
            }
            markers.push(drawing.mapobj);
          }
        }
      });

      clusterMarkers(markers);
    }
  };

  useMemo(() => updateElementsOnMap(map), 
    [map]);

  useMemo(() => updateElementsOnMap(map, true), 
    [elements, typeFilter, selectedName]);

  return null; // no html render, draw on map directly instead
};

export default Drawings;