import create from 'zustand';

const initAppData = () => appStoreApi.getState().initAppData();
const showOkDialog = (okMessage) => appStoreApi.getState().showOkDialog(okMessage);
const setExpFileName = (fName) => appStoreApi.getState().setExpFileName(fName);
const getExpFileName = () => appStoreApi.getState().expFileName;
const setDeleteExport = (del) => appStoreApi.getState().setDeleteExport(del);
const getDeleteExport = () => appStoreApi.getState().deleteExport;

const [useAppStore, appStoreApi] = create((set, get) => ({
  initialized: false,
  okMessage: null,
  expFileName: 'mapDemoData',
  deleteExport: false,

  initAppData: () => {
    const initialized = get().initialized;
    if (!initialized) {
      const expFileName = localStorage.getItem('expFileName');
      if (expFileName) set({ expFileName: expFileName });
      
      const deleteExportStr = localStorage.getItem('deleteExport');
      const deleteExport = deleteExportStr === 'true';
      set({ deleteExport: deleteExport });

      set({ initialized: true });
    }
  },

  showOkDialog: (okMessage) => set({ okMessage: okMessage }),

  setExpFileName: (fName) => {
    set({ expFileName: fName });
    localStorage.setItem('expFileName', fName);
  },

  setDeleteExport: (del) => {
    set({ deleteExport: del });
    localStorage.setItem('deleteExport', del);
  },

}));

export { 
  useAppStore,
  initAppData,
  showOkDialog,
  setExpFileName,
  getExpFileName,
  setDeleteExport,
  getDeleteExport
};