import { Button } from '@mui/material';
import { styled, makeStyles } from '@mui/styles';
import { Icon } from '@mui/material';
import SrtIcon from '@mui/icons-material/Sort';
import { markerRadiusSvg, pencilSvg, trashCanSvg } from './svg';


const SizedIcon = styled(Icon)({
  height: '24px', 
  width: '24px',
});

const useStyles = makeStyles({
  coloredIcon: {
    cursor: 'pointer',
    color: 'RoyalBlue',
    '&:hover': {color: 'Magenta'}
  },
});


// makeStyles doesn't work with props
const SortIcon = (props) => {
  const {active, asc,  ...rest} = props;
  const actSt = props.active ? 'colorBlack' : 'disablSort';
  const ascSt = props.asc ? ' upend' : '';
  return <SrtIcon className={actSt + ascSt} {...rest}/>;
}

const LocateIcon = (props) => {
  const { coloredIcon } = useStyles();
  return <SizedIcon {...props} className={`${coloredIcon}`}>{markerRadiusSvg}</SizedIcon>;
}

const EditIcon = (props) => {
  const { coloredIcon } = useStyles();
  return <SizedIcon {...props} className={`${coloredIcon}`}>{pencilSvg}</SizedIcon>;
}

const DeleteIcon = (props) => {
  const { coloredIcon } = useStyles();
  return <SizedIcon {...props} className={`${coloredIcon}`}>{trashCanSvg}</SizedIcon>;
}

const ButtonColored = styled(Button)({
  fontSize: 10,
  color: 'white',
  backgroundColor: 'Coral',
  '&:hover': {
    backgroundColor: 'OrangeRed',
  },
});

export {
  SortIcon,
  LocateIcon,
  EditIcon,
  DeleteIcon,
  ButtonColored
};