import React, { useMemo } from 'react';
import MuiDrawer from '@mui/material/Drawer';
import useWindowSize from "../hooks/useWindowSize";

const Drawer = ({open, body}) => {  
  const wndSize = useWindowSize();
  const landscape = useMemo(() => wndSize.width > wndSize.height, [wndSize]);

  const stBody = useMemo(() => 
    <div className = {landscape ? 'width25' : 'height21'}>{body}</div>
  , [landscape, body]);

  return <>
    <MuiDrawer
      variant="persistent"
      anchor={landscape ? 'right' : 'bottom'}
      open={open}
    >
      {stBody}
    </MuiDrawer>
  </>;
}

export default Drawer;